.image-thing-container{
	width: 100%;
	height:100%;
}

.image-thing-image{
	height: calc(100% - 60px);
    width: 100%;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.image-thing-description{
	color: #fff;
	width: 100%;
	font-weight: 300;

    position: relative;
    top: -80px;
    font-size: 2em;
	padding: 10px;
	background: -webkit-gradient(linear, left bottom, left top, from(rgba(0,0,0, 0.8)), to(rgba(0,0,0,0)));
	background: -webkit-linear-gradient(bottom, rgba(0,0,0, 0.8), rgba(0,0,0,0));
	background: -o-linear-gradient(bottom, rgba(0,0,0, 0.8), rgba(0,0,0,0));
	background: linear-gradient(to top, rgba(0,0,0, 0.8), rgba(0,0,0,0));
}

.image-thing-description span{
	display: block;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
	-webkit-transform: translateY(40%);
	    -ms-transform: translateY(40%);
	        transform: translateY(40%);
	opacity: 0;
	-webkit-transition: 0.4s cubic-bezier(0.19, 1, 0.22, 1) all;
	-o-transition: 0.4s cubic-bezier(0.19, 1, 0.22, 1) all;
	transition: 0.4s cubic-bezier(0.19, 1, 0.22, 1) all;
	-webkit-transition-delay: 0.6s;
	     -o-transition-delay: 0.6s;
	        transition-delay: 0.6s;
}
.active .image-thing-description span{
	-webkit-transform: translateY(0);
	    -ms-transform: translateY(0);
	        transform: translateY(0);
	opacity: 1;
}


